<template>
  <div
    class="d-flex justify-content-center h-100 align-items-center"
    style="min-width: 40vw"
  >
    <div>
      <span @click="setting" class="me-1">Solar</span>
      <b @click="reload">{{ this.power }} W</b>
    </div>
    <div
      v-if="this.showSetting"
      class="
        position-absolute
        top-0
        start-0
        vh-100
        vw-100
        d-flex
        justify-content-center
        align-items-center
        backdrop
      "
    >
      <div id="set" class="bg-black shadow text-white p-3 rounded">
        <div class="d-flex">
          <p class="display-6">Settings</p>
          <span class="flex-grow-1"></span>
          <button @click="setting" class="btn-close btn-close-white" />
        </div>
        <div class="btn-group">
          <button
            class="btn btn-dark w-auto position-unset"
            @click="promt({ type: 'server_url', title: 'Server Url' })"
          >
            Server
          </button>
          <button
            class="btn btn-dark w-auto position-unset"
            @click="promt({ type: 'device_id', title: 'Device ID' })"
          >
            Device
          </button>
          <button
            class="btn btn-dark w-auto position-unset"
            @click="promt({ type: 'auth_key', title: 'Token' })"
          >
            Token
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from '../../node_modules/axios'
import moment from '../../node_modules/moment'
export default {
  name: 'SolarInfoBoard',
  data () {
    return {
      power: null,
      timestamp: null,
      showSetting: false,
      auth_key: '',
      device_id: '',
      server_url: ''
    }
  },
  methods: {
    moment: function (time) {
      return moment(time)
    },
    reload: function () {
      this.power = 0
      const payload =
        'id=' + this.device_id + '&auth_key=' + this.auth_key
      if (
        this.server_url !== '' &&
        this.device_id !== '' &&
        this.auth_key !== ''
      ) {
        axios
          .post(this.server_url, payload)
          .then((response) => {
            this.power = response.data.data.device_status.meters[0].power
            this.timestamp =
              response.data.data.device_status.meters[0].timestamp
          })
      }
    },
    setting: function () {
      this.showSetting = !this.showSetting
    },
    promt: function (attr) {
      let defaultValue = ''
      switch (attr.type) {
        case 'auth_key':
          defaultValue = this.auth_key
          break
        case 'device_id':
          defaultValue = this.device_id
          break
        case 'server_url':
          defaultValue = this.server_url
          break
      }
      defaultValue = prompt(attr.title)
      if (
        defaultValue !== undefined &&
        defaultValue !== null &&
        defaultValue !== ''
      ) {
        switch (attr.type) {
          case 'auth_key':
            this.auth_key = defaultValue
            break
          case 'device_id':
            this.device_id = defaultValue
            break
          case 'server_url':
            this.server_url = defaultValue
            break
        }
      }
    }
  },
  mounted () {
    if (localStorage.auth_key) {
      this.auth_key = localStorage.auth_key
    }
    if (localStorage.device_id) {
      this.device_id = localStorage.device_id
    }
    if (localStorage.server_url) {
      this.server_url = localStorage.server_url
    }
    this.reload()
  },
  watch: {
    auth_key (newName) {
      localStorage.auth_key = newName
    },
    device_id (newName) {
      localStorage.device_id = newName
    },
    server_url (newName) {
      localStorage.server_url = newName
    }
  }
}
</script>

<style scoped>
#set {
  max-width: 90vw;
  z-index: 20;
}
.position-unset {
  position: unset !important;
}
</style>
