<template>
  <div v-if="info" class="d-flex flex-column vw-100 vh-100">
    <div class="d-flex justify-content-around">
      <Solar/>
      <div style="min-width:40vw">
        <span class="mt-4 mb-4"><img width="100px" :src="image(icon)" /></span>
        <span class="display-1 mb-4">{{ parseInt(current) }} &#176;</span>
      </div>
    </div>
    <div class="d-flex justify-content-around">
      <shortLayout title="MaxTemp" :value="max" code="°" />
      <shortLayout title="MinTemp" :value="min" code="°" />
    </div>
    <div class="d-flex justify-content-around">
      <shortLayout title="Humidity" :value="humidity" code="%" />
      <shortLayout title="RealFeel" :value="feels_like" code="°" />
    </div>
    <div class="flex-grow-1"></div>
    <div
      class="d-flex flex-row flex-nowrap overflow-auto vw-100"
      style="height: 30vh; margin: 0px; scroll-snap-type: x mandatory"
    >
      <div
        v-for="item in info.data.list"
        :key="item.dt"
        class="
          card
          ms-3
          me-3
          d-flex
          p-2
          justify-content-between
          mb-3
          rounded-4
          shadow
        "
        style="min-width: 40vw; scroll-snap-align: center"
      >
        <span> {{ moment(item.dt_txt).format("HH") }} Uhr</span>
        <span><img width="100px" :src="image(item.weather[0].icon)" /></span>
        <span class="d-none"> {{ item.weather[0].description }}</span>
        <span> {{ parseInt(item.main.temp) }} &#176;</span>
      </div>
    </div>
    <div class="m-4 card rounded-pill shadow">
      <div class="d-flex justify-content-evenly">
        <button class="btn btn-dark rounded-circle">
          <RefreshCwIcon @click="reload" />
        </button>
      </div>
    </div>
  </div>
</template>

<script>
import shortLayout from './shortLayout.vue'
import axios from '../../node_modules/axios'
import moment from '../../node_modules/moment'
import { RefreshCwIcon } from '../../node_modules/vue-feather-icons'
import Solar from './solar.vue'
export default {
  components: { shortLayout, RefreshCwIcon, Solar },
  name: 'CurrentWeather',
  data () {
    return {
      info: null,
      max: 0,
      min: 0,
      current: 0,
      icon: '',
      humidity: 0,
      feels_like: 0
    }
  },
  methods: {
    moment: function (time) {
      return moment(time)
    },
    image: function (code) {
      return 'http://openweathermap.org/img/wn/' + code + '@2x.png'
    },
    maxTemp: function (list) {
      list.forEach((element) => {
        if (this.max < element.main.temp_max) {
          this.max = parseInt(element.main.temp_max)
        }
        if (this.min === 0) {
          this.min = element.main.temp_min
        }
        if (this.humidity === 0) {
          this.humidity = parseInt(element.main.humidity)
        }
        if (this.min > element.main.temp_min) {
          this.min = parseInt(element.main.temp_min)
        }
        if (this.current === 0) {
          this.current = element.main.temp
        }
        if (this.feels_like === 0) {
          this.feels_like = parseInt(element.main.feels_like)
        }
        if (this.icon === '') {
          this.icon = element.weather[0].icon
        }
      })
    },
    weather: function () {
      axios
        .get(
          'https://api.openweathermap.org/data/2.5/forecast/?q=Berlin,de&appid=8021b4e46c033f8148e926c465998bee&units=metric&lang=de&cnt=10'
        )
        .then((response) => {
          this.info = response
          this.maxTemp(response.data.list)
        })
    },
    reload: function () {
      this.weather()
    }
  },
  mounted () {
    this.weather()
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.card:first-child {
  margin-left: calc(10vw - 0.5rem) !important;
}
.btn {
  margin: -10px;
  padding: 10px;
  height: 50px;
  width: 50px;
}
</style>
